/**
 * 
 * `<body>``height: 100%;``overflow: hidden`
 * https://cferdinandi.github.io/smooth-scroll/options.html
 * 
 * <a class="js-smoothScroll" href="#"></a>
 *
 * id
 * <header class="sw-Header"></header>
 * <a class="js-smoothScroll" href="#section1"></a>
 * <div id="section1"></div>
 */
export default function jsSmoothScroll () {
  if($('.js-smoothScroll').length) {
    smoothScroll.init({
      selector: '.js-smoothScroll', // 
      ignore: '.js-smoothScroll-ignore', // 
      selectorHeader: null, // JQuery`.sw-Header`HTML
      speed: 500, // ()
      easing: 'easeInOutCubic', // 
      offset: 0 // 
    });
  }
};
