/**
 * 
 * https://github.com/liabru/jquery-match-height#options
 * 
 * <div class="parent">
 *   <div class="child js-alignHeight"></div>
 *   <div class="child js-alignHeight"></div>
 * </div>
 */
export default function jsAlignHeight () {
  if($('.js-alignHeight').length) {
    $('.js-alignHeight').matchHeight();
  }
};
